:root
{
	--navtextcolor: grey;
	--navcolor: white;
	
	--maincolor: #d7daea;
	--secondmaincolor: #e6e7ef;
	--titletextcolor: black;
	
	--color1: #F75C03;
	--color2: #FFC857;
	--color3: #0E79B2;
	
	--text: black;
	--invertext: white;
	color: var(--text);

	--gradient1: linear-gradient(var(--color1),var(--color2));
	--largefont: 'Ubuntu', sans-serif;
	--regularfont: 'Noto Sans', sans-serif;
}

body
{
	margin: 0px;
  font-family: var(--regularfont);
	background-color: var(--navcolor);
	transition: 0.2s;
}

nav
{
	width: 100%;
	display: flex;
	justify-content: space-between;
	height: 75px;
	color: var(--navtextcolor);
	font-size: 1.5rem;
  font-family: var(--largefont);
	font-weight: bold;
	background-color: var(--navcolor);
}


@media only screen and (max-width: 600px) {
  .YuMe
	{
		display: none;
	}
	.YM
	{
		display: block;
	}
}


@media only screen and (min-width: 601px) {
  .YuMe
	{
		display: block;
	}
	.YM
	{
		display: none;
	}
}

nav div
{
	display: flex;
	align-items: center;
	padding-left: 15px;
	min-width: 100px;
}


nav ul
{
	width: 100%;
	height: 100%;
	list-style: none;
	display: flex;
	justify-content: space-around;
	margin: 0px;
	padding: 0px;
	align-items: center;
	align-content: center;
}

nav ul li
{
	display: flex;
	flex-direction: row-reverse;
	justify-content: center;
	padding-left: 10px;
	padding-right: 10px;
	transition: 0.2s, display 0s, background-color 0s;
	width: 40px;
	align-content: center;
	align-items: center;
	height: 100%;
	cursor: pointer;

	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

nav ul li:hover
{
	color: var(--color3);
	width: 110px;
	background-color: var(--maincolor);
	transition: 0.2s;
}

nav ul li span
{

	color: var(--navcolor);
	display: none;
	overflow: hidden;
	transition: 0.2s;
}

nav ul li:hover > span
{
	color: var(--color3);
	display: inline;
	transition: 0.2s;

}

main
{
	background-color: var(--maincolor);
	height: auto;
	width: 100%;
	padding-bottom: 200px;
}

.home
{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	align-items: center;
	align-content: center;
	height: 100vh;
	min-height: 700px;
	margin-left: 5%;
	margin-right: 5%;
	padding: auto;
	align-self: center;
}

.home div
{
	display: flex;
	width: 400px;
	min-width: 200px;
	flex-direction: column;
	justify-content: space-around;
	text-align: center;
  justify-content: center;
  align-items: center;
  align-content: normal;
	font-family: var(--largefont);
}

.home div img
{
	max-width: 500px;
	max-height: 500px;
	min-width: 300px;
	min-height: 300px;
	width: 100%;
	height: 100%;
	border-radius: 5px;

	-webkit-box-shadow: 0px 0px 8px 4px rgba(0,0,0,0.11); 
	box-shadow: 0px 0px 8px 4px rgba(0,0,0,0.11);
}

.container
{
	width: 100%;
	max-width: 1200px;
	margin: auto;

	-webkit-box-shadow: 0px 0px 8px 4px rgba(0,0,0,0.18); 
	box-shadow: 0px 0px 8px 4px rgba(0,0,0,0.18);
}

.home code
{
  font-family: var(--largefont);
}

.hometext
{
	margin: 25px;
}


.home div p
{
	font-weight: 400;
	font-size: 1.2em;
}

.gradienttext1
{
  background: var(--gradient1);
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  -webkit-text-fill-color: transparent;
}

.heading
{
	width: 100%;
	text-align: center;
  font-family: var(--largefont);
	font-weight: bold;
	font-size: 2rem;
	margin-top: 60px;
	margin-bottom: 60px;
}


.cardbox
{
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	align-items: normal;
	justify-content: space-around;
	margin-bottom: 50px;
}

.cardbox .card
{
	display: flex;
	width: 500px;
	height: 660px;
	margin: 10px;
	margin-bottom: 25px;
	flex-direction: column;
	/*border: 3px dashed var(--color1);*/
	background-color: var(--navcolor);
	-webkit-box-shadow: 0px 0px 8px 4px rgba(0,0,0,0.09); 
	box-shadow: 0px 0px 8px 4px rgba(0,0,0,0.09);
	border-radius: 5px;


	display: flex;
	padding: 15px;
	justify-content: space-between;
}

.card h1
{
	width: 100%;
	text-align: center;

  font-family: var(--largefont);
	font-weight: bold;
	margin-top: 2px;
	margin-bottom: 2px;

}

.card h1 a
{
	color: var(--text);
	text-decoration: none;

}

.card img
{
	width: 92%;
	margin: 4%;
	border-radius: 5px;
}

.card p
{
	font-size: 1em;
}

.regulartext
{
	margin-left: 5%;
	margin-right: 5%;
}

.aboutcontainer
{

	margin-top: 30px;
	margin-bottom: 30px;
}

.aboutme
{
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	flex-wrap: wrap;
	margin-top: 50px;
	margin-bottom: 50px;
}

.aboutme div
{
	min-width: 280px;
	min-height: 130px;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	align-content: center;
	max-height: 100px;
}

.button
{
	border: 3px solid var(--color1);
	height: 60px;
	width: 280px;
	align-content: center;
	align-items: center;
	justify-content: center;
	display: flex;
	/*background-image: var(--gradient1);*/
	border-radius: 5px;
	box-shadow: 0px 0px 11px 3px rgba(0,0,0,0.13);
	transition: 0.2s;
	text-decoration: none;
	font-size: 1.2rem;
  font-family: var(--largefont);
	font-weight: bold;
	color: var(--text);
	cursor: pointer;

}

.articleButton{
	cursor: pointer;
}

.button:hover
{
	height: 70px;
	width: 300px;
	background-color: var(--color1);
	color: var(--invertext);
}

.aboutme div a img
{
	width: 30px;
	height: 30px;
	margin: 15px;
	display: inline-block;

}

.link
{
	color: var(--color3);
	text-decoration: none;	
}

.link:hover
{
	text-decoration: underline;
}

footer
{
	background-color: var(--navcolor);
	height: 300px;
	display: flex;
	justify-content: space-around;

}

footer div
{
	align-content: center;
	align-items: center;
	display: flex;
}

.articleList
{
	background-color: var(--maincolor);
	padding-top: 10px;
	padding-bottom: 10px;
	margin-top: 0px;
	margin-bottom: 0px;
	padding-left: 5%;
	padding-right: 5%;

}

.articleList ul
{
	padding-left: 0px;
}

.articleList ul li
{
	font-size: 1.5rem;
	font-family: var(--regularfont);
	list-style: none;
	width: 100%;
	background-color: var(--navcolor);
	margin-top: 20px;
	margin-bottom: 20px;
	border-radius: 5px;
	height: 60px;
	display: flex;
	align-items: center;
	align-content: center;
	transition: 0.1s;
	display: flex;
	justify-content: space-between;

	overflow: hidden;
}


.articleList ul li:hover
{
	color: var(--color1);
}

.articleList ul li:hover > .slidebutton
{
	right: 0px;
}

.articleList ul li p
{
	margin-left: 20px;
}

.slidebutton
{
	float: right;
	position: relative;
	right: -40px;
	transition: 0.1s;
	height: 70px;
	width: 55px;
	align-items: center;
	align-content: center;
	display: flex;
	justify-content: center;
	background-color: var(--secondmaincolor);
	color: var(--color1);
}

.article
{
	background-color: var(--maincolor);
	padding: 5%;

}
